* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main a {
  text-decoration: none;
  color: white;
}

#style {
  margin-top: 10px;
  position: fixed;
  width: 100%;
  align-items: center;

  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  padding: 0 20px;
}
#box-nav {
  display: flex;
  gap: 50px;

  margin-left: 700px;
}

.img {
  letter-spacing: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: white;
  height: 500px;
  width: 100%;
  background-image: url(/src/assets/tech-assts.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#btn {
  border-radius: 25px 25px 25px 25px;
  padding: 10px 50px 10px 50px;
}

/* for color-style */
#box-nav {
  border: 1px solid black;
  padding: 10px 20px 10px 20px;
  background-color: rgb(0, 0, 0, 0.6);
  border-radius: 25px 25px 25px 25px;
}

